
:global(#contact) {
	background-color: #fccbd3;
}

/* Form */

.formContainer {
	display: flex;
	justify-content: center;
	width: max-content;
	padding: 20px 30px;
	box-sizing: border-box;
	background-color: white;
	border-radius: 10px;
}
.form {

	display: grid;
	grid-auto-flow: row;
	text-align: left;
	width: 500px;
	max-width: 100%;
	row-gap: 10px;
}

.form input, .form textarea {
	outline: none;
	border: none;
	border-radius: 5px;
	min-height: 2em;
	padding: 10px;
	box-sizing: border-box;
	border: #02d184 2px solid;
	border-radius: 5px;
}

.form textarea {
	min-height: 10em;
}

.form input[type='submit'] {
	border: none;
	background-color: white;
	font-family: 'Montserrat',"Source Sans Pro","Open Sans","San Francisco",Helvetica,Arial,sans-serif;
	font-size: small;
	background-color: #02d18440;
	transition: background-color 0.33s ease;
}
.form input[type='submit']:hover {
	background-color: #02d184;
}