@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
		'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
		sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
		monospace;
}
/* Depth: 0 */
html {
	scroll-behavior: smooth;
}

#root {
	display: flex;
	font-family: 'Montserrat',"Source Sans Pro","Open Sans","San Francisco",Helvetica,Arial,sans-serif;
	text-align: center;
    justify-content: center;
	font-size: larger;
	background-color: #f6f8f7;
}

/* Depth: 1 */

.background-gradient {
	background-color: white;
	z-index: -2;
	-webkit-filter: brightness(1.8);
	        filter: brightness(1.8);
	position: fixed;
	height: 100%;
	width: 100%;
}

.background {
	opacity: 0.5;
	background-repeat: no-repeat;
	background-size: cover;
	background-position: 50%;
	background-color: white;
	position: fixed;
	height: 100%;
	width: 100%;
}

.main {
	display: flex;
	flex-direction: column;
	padding-top: 8vh;
	z-index: 1;
	width: 100%;
}
.main * {
	scroll-margin-top: 6vh;
}

/* Depth: 2 */

.main > section, .main > div {
    flex: 0 1 auto;
	flex-shrink: 0;
	display: flex;
	flex-direction: column;
	align-items: center;
	padding: 50px 0px 30px 0px;
	
}

.main > section a:link,
.main > section  a:visited,
.main > section a:hover,
.main > section a:active {
	color: inherit;
	font-weight: bold;
	-webkit-text-emphasis: none;
	        text-emphasis: none;
	text-decoration: none;
	cursor: pointer;
}

.main section .wrapper > * {
	margin: 20px 0 10px 0;
}

@media only screen and (max-width: 700px) {
	.App {
		display: flex;
		flex-direction: column;
	}
	@supports not ((display: flex) or (display: grid)) {
		.main {
			display: flex;
		}
	}
}

/* Depth: 3 */

.wrapper {
	width: 1040px;
	max-width: 95%;
	display: flex;
	flex-direction: column;
	align-items: center;
}
.wrapper > :not(div):not(a):not(img) {
	align-self: flex-start;
	text-align: left;
}

/* Depth: 4 */

.columns {
	display: grid;
	width: 100%;
	grid-template-columns: 1fr 1fr;
	grid-column-gap: 50px;
	-webkit-column-gap: 50px;
	        column-gap: 50px;
	grid-row-gap: 30px;
	row-gap: 30px;
}
@media only screen and (max-width: 600px) {
	.columns {
		grid-template-columns: unset;
		grid-auto-rows: auto;
	}

}
.column-text {
	display: flex;
	flex-direction: column;
	text-align: left;
	box-sizing: border-box;
	align-items: stretch;
	justify-content: center;
}

/* Additional */

.column-text > *:first-child {
	-webkit-margin-before: 0px;
	        margin-block-start: 0px;
}
p:first-of-type {
    -webkit-margin-before: 0px;
            margin-block-start: 0px;
}
ol, ul {
	-webkit-margin-before: 0.5em;
	        margin-block-start: 0.5em;
	-webkit-margin-after: 0px;
	        margin-block-end: 0px;
}

.normal {
	font-weight: normal;
}

.highlighted {
	color: rgb(233, 198, 0);
}
@supports (-webkit-text-stroke: 1px black) {
	.highlighted {
	  -webkit-text-stroke: 0.5px black;
	}
}

.bold {
	font-weight: bold;
}

.bolder {
	font-weight: bolder;
}

.underlined {
	text-decoration: underline;
}

.strikethrough {
	text-decoration: line-through;
}

.main label, .light {
	font-weight: 300;
}

/* Buttons */

.buttonSection {
	display: grid;
	grid-auto-columns: 1fr;
	grid-auto-flow: column;
	justify-content: center;
	width: -webkit-max-content;
	width: -moz-max-content;
	width: max-content;
}

@media only screen and (max-width: 700px) {
	.buttonSection {
		grid-auto-rows: 1fr;
		grid-auto-flow: row;
	}
}

.main section a.download, .main section div.download {
	margin: 10px 30px;
	border: 0;
	padding: 1em 75px;
	border-radius: 5px;
	box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.2);
	font-size: 14px;
	position: relative;
	top: -2px;
	white-space: nowrap;
	max-width: 10vw;
	display: flex;
	justify-content: center;
	font-weight: normal;
	text-shadow: none;
}

@media only screen and (max-width: 700px) {
	.download {
		margin: 10px 0;
	}
}

.main section a.download:hover, .main section div.download:hover {
	top: 0px;
}

.main section a.platform {
	color: black;
	background-color: white;
}

a.platform > svg {
	padding: 0 5px;
}

.main section a.web {
	color: white;
	background-color: #7289da;
}

/* General Table Styles */



table {
	width: 100%;
}

table tr {
	width: 100%;
}

table thead tr {
    background-color: rgba(134, 134, 255, 0.8);
	text-shadow: 1px 1px 2px black;
}

table > tbody tr:nth-child(odd) {
    background-color: rgba(53, 135, 175, 0.31);
}

