

/* Sections */

.imageContainer {
	position: relative;
	display: grid;
}
.screenshot {
	max-width: 300px;
	position: relative;
	grid-column: 1;
	grid-row: 1;
	box-shadow: 2px 2px 6px rgba(0, 0, 0, 0.2);
	transition: all 1s ease!important;
}

.body {
	animation: fade 1s ease both;
}

@keyframes fade {
	from {
		opacity: 0
	}
	to {
		opacity: 1
	}
}