:global(#home) {
	background-color: #02d184;
	color: white;
}
:global(#home) :global(.wrapper) > * {
	text-align: center;
	width: 100%;
}

:global(#works) {
	background-color: #fdd3da;
}

.description {
	color: rgba(20, 20, 20. 0.8);
	display: block;
	font-size: 18px;
	line-height: 1.5em;
}

.title {
	font-weight: 700;
	font-size: 48px;
}
.subtitle {
	width: 100%;
	padding-top: 20px;
	font-weight: 400;
	font-size: larger;
}

/* Notice */

:global(#notice) :global(.wrapper) > * {
	text-align: center;
	width: 100%;
}